<h1 id="贡献代码" tabindex="-1"><a class="header-anchor" href="#贡献代码" aria-hidden="true">#</a> 贡献代码</h1>
<h2 id="开发" tabindex="-1"><a class="header-anchor" href="#开发" aria-hidden="true">#</a> 开发</h2>
<p>我们欢迎广大开发者贡献大家的智慧，让我们共同让它变得更完美.</p>
<h3 id="开始之前" tabindex="-1"><a class="header-anchor" href="#开始之前" aria-hidden="true">#</a> 开始之前</h3>
<p>请严格遵循以下代码标准:</p>
<blockquote>
<ul>
<li><a href="https://github.com/php-fig/fig-standards/blob/master/accepted/PSR-2-coding-style-guide.md" target="_blank" rel="noopener noreferrer">PSR-2<OutboundLink/></a>.</li>
<li>使用 4 个空格作为缩进。</li>
</ul>
</blockquote>
<h3 id="流程" tabindex="-1"><a class="header-anchor" href="#流程" aria-hidden="true">#</a> 流程</h3>
<ol>
<li>Fork <a href="https://github.com/overtrue/wechat" target="_blank" rel="noopener noreferrer">overtrue/wechat<OutboundLink/></a> 到本地.</li>
<li>创建新的分支：</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>    $ <span class="token function">git</span> checkout -b new_feature
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ol start="3">
<li>编写代码。</li>
<li>Push 到你的分支:</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>    $ <span class="token function">git</span> push origin new_feature
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ol start="5">
<li>创建 Pull Request 并描述你完成的功能或者做出的修改。</li>
</ol>
<blockquote>
<p>{warning} 注意：注释请使用英文</p>
</blockquote>
<h2 id="更新文档" tabindex="-1"><a class="header-anchor" href="#更新文档" aria-hidden="true">#</a> 更新文档</h2>
<p>我们的文档也是开源的，源代码在 <a href="https://github.com/EasyWeChat/docs" target="_blank" rel="noopener noreferrer">EasyWeChat/docs<OutboundLink/></a>。</p>
<h3 id="流程-1" tabindex="-1"><a class="header-anchor" href="#流程-1" aria-hidden="true">#</a> 流程</h3>
<ol>
<li>Fork <a href="https://github.com/EasyWeChat/docs" target="_blank" rel="noopener noreferrer">EasyWeChat/docs<OutboundLink/></a></li>
<li>Clone 到你的电脑：</li>
</ol>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>    $ <span class="token function">git</span> clone https://github.com/<span class="token operator">&lt;</span>username<span class="token operator">></span>/site.git
    $ <span class="token builtin class-name">cd</span> docs
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><ol start="3">
<li>创建新的分支，编辑文档</li>
<li>Push 到你的分支。</li>
<li>创建 Pull Request 并描述你完成的功能或者做出的修改。</li>
</ol>
<h2 id="报告-bug" tabindex="-1"><a class="header-anchor" href="#报告-bug" aria-hidden="true">#</a> 报告 Bug</h2>
<p>当你在使用过程中遇到问题，请查阅 <RouterLink to="/4.x/troubleshooting.html">疑难解答</RouterLink> 或者在这里提问 <a href="https://github.com/overtrue/wechat/issues" target="_blank" rel="noopener noreferrer">GitHub<OutboundLink/></a>. 如果还是不能解决你的问题，请到 GitHub 联系我们。</p>
